%h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 46px;
  margin: 0;
}

html,
body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
img,
form,
footer,
header,
section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;

  vertical-align: baseline;
}
